@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.types--container {
  margin: 50px;
  text-align: center;
}

.dropdown {
  font-family: "Poppins", sans-serif;
}

/* Dropdown Item Container */
.types--container-item {
  position: relative;
  cursor: pointer;
  font-size: 15px;
  padding: 18px 20px;
  width: 90%;
  margin: 10px 15px;
  border-radius: 5px;
}

.types--container-item:hover {
  border: 1px solid #fdc8e1;
  background-color: #ffeff7;
}

.types--text {
  margin-left: 25px;
}

/* Hide the default button */
.types--container-item input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Custom checkbox */
.types--checkmark {
  position: absolute;
  top: 19px;
  left: 20px;
  height: 20px;
  width: 20px;
  background-color: white;
  border: 0.5px solid #ccc;
  border-radius: 4px;
}

/* When the checkbox is checked, add a blue background */
.types--container-item input:checked ~ .types--checkmark {
  background-color: #f981ba;
  border: none;
}

/* Create the checkmark/indicator (hidden when not checked) */
.types--checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.types--container-item input:checked ~ .types--checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.types--container-item .types--checkmark:after {
  left: 8px;
  top: 3px;
  width: 5px;
  height: 12px;
  border: solid white;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}

.types--dropdown-container {
  width: 300px;
  box-shadow: 3px 3px 25px rgb(210, 210, 210);
  border-radius: 10px;
}

.types--toggle {
  border: 2px solid black;
  color: rgb(164, 160, 160);
  width: 300px;
  text-align: left;
}

.types--toggle:hover {
  background-color: rgb(249, 232, 245);
  color: rgb(164, 160, 160);
}

.types--toggle::after {
  margin-left: 185px;
  color: black;
}

/* customise Toggle Switch */
.react-switch-bg {
  border: 1px solid pink;
}

.settings--toggle-background {
  background-color: #ffffff;
  border: none;
}

.settings--toggle-color-black {
  color: black;
}

.settings--toggle-background:hover {
  background-color: #f9e6ef;
  border: 1px solid #f9b4d5;
  color: black;
}

.settings--toggle-background:active {
  background-color: #ffffff;
}

.show > .btn-primary.dropdown-toggle {
  color: black;
  background-color: #f9e6ef;
  border: #f9b4d5;
}

.settings--container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 5px 5px 15px rgb(217, 217, 217);
  margin: 50px auto;
}

.days--btn {
  border: none;
  background-color: black;
  color: #ffffff;
  border-radius: 5px;
  padding: 10px 25px;
  transition: 0.3s ease;
  cursor: pointer;
  margin-left: 23px;
  width: 200px;
  height: 50px;
  margin-bottom: 30px;
}

.days--btn:hover {
  color: #ffffff;
  background-color: #3b3b3b;
}

.days--container {
  width: 250px;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
  box-shadow: 5px 5px 10px rgb(197, 197, 197);
  margin: 50px auto;
}

.days--text {
  margin-left: 33px;
  margin-bottom: 0px;
  display: inline-block;
  margin-top: 15px;
  font-weight: bold;
}

